const transformListMessage = (list) => {
  return list.map((item) => {
    return {
      id: item.id_room,
      avatar: item.creator_avatar,
      name: item.creator_name,
    };
  });
};

const TabMessage = () => {
  const { url } = useRouteMatch();
  const listMessage = transformListMessage(
    useSelector(selectListConversation) || []
  );

  const isLoading = useSelector(selectConversationLoading);

  return (
    <div className='message'>
      <div className='message__filter'>
        <Select
          options={[
            'Tt c tin nhn',
            'Ch tin nhn cha c',
            'Ch tin nhn t ngi l',
          ]}
          defaultOption={0}
        />
        <p>nh du l  c</p>
      </div>
      <div className='message__content'>
        {isLoading && <SpinLoading />}
        <ul>
          {listMessage.map((item) => (
            <li key={item.id}>
              <Link to={`${url}/${item.id}`}>
                <MessageItem item={item} />
              </Link>
            </li>
          ))}
        </ul>
      </div>
    </div>
  );
};

export default TabMessage;
